import React, { useState } from 'react';
import yosan from './r4tousho.json';
import './App.css';

import { FormControl, FormControlLabel, FormLabel, Radio, RadioGroup, Slider, Box, Grid } from '@mui/material';

import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import ArticleIcon from '@mui/icons-material/Article';
import TagFacesIcon from '@mui/icons-material/TagFaces';
import LocalHospitalIcon from '@mui/icons-material/LocalHospital';
import EngineeringIcon from '@mui/icons-material/Engineering';
import AgricultureIcon from '@mui/icons-material/Agriculture';
import CurrencyYenIcon from '@mui/icons-material/CurrencyYen';
import EditRoadIcon from '@mui/icons-material/EditRoad';
import FireTruckIcon from '@mui/icons-material/FireTruck';
import SchoolIcon from '@mui/icons-material/School';
import MoodBadIcon from '@mui/icons-material/MoodBad';
import QuestionMarkIcon from '@mui/icons-material/QuestionMark';
import SavingsIcon from '@mui/icons-material/Savings';

interface yosanObject {
  // "year": number,
  [key: string]: himoku
}
interface himoku {
  label: string,
  value: number
}
interface hogehoge {
  [key: string]: number
}

function App() {
  const [income, setIncome] = useState(3000000);
  const [single, setSingle] = useState(true);
  const [annual_tax, setAnnual_tax] = useState(180000);
  const [himoku_list, setHimoku_list] = useState<hogehoge>();

  const Item = (props: { label: string, icon: any }) => {
    let yosan_: yosanObject = yosan;
    return (
      <>
        <div>{yosan_[props.label].label}</div>
        {props.icon}
        <div>{himoku_list![props.label]}</div>
        <hr></hr>
      </>
    )
  }

  function valuetext(value: number) {
    return `\\${value}`;
  }

  const handleRadio = (event: React.ChangeEvent<HTMLInputElement>, value: string) => {
    if (value === "single")
      setSingle(true);
    else
      setSingle(false);
  }

  const handleChange = (event: Event, newValue: number | number[]) => {
    setIncome(newValue as number);
    if (single)
      setAnnual_tax(newValue as number * 0.06); // 
    else
      setAnnual_tax((newValue as number - 500000) * 0.06); // 
    usage();
  }

  const usage = () => {
    let sum = 0;
    let yosan_: yosanObject = yosan;
    Object.keys(yosan_).forEach((key) => {
      sum += yosan_[key].value;
    });
    // 
    // let proportions: hogehoge = {};
    let your_moneys: hogehoge = {};
    Object.keys(yosan_).forEach((key) => {
      your_moneys[key] = Math.round(yosan_[key].value / sum * annual_tax);
    });
    setHimoku_list(your_moneys);

    // return <></>
  }

  const makeGrid = () => {
    if (himoku_list)
      return (
        // <Grid container spacing={2}>
        //   <Grid item xs={2}>: {himoku_list["council"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["general_affair"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["social_welfare"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["public_health_and_sanitation"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["labor"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["agriculture_forestry_and_fisheries"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["commerce_and_industry"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["civil_engineering"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["firefighting"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["education"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["debt_service"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["sundry"]}</Grid>
        //   <Grid item xs={2}>: {himoku_list["reserve"]}</Grid>
        // </Grid>
        <Grid container spacing={2}>
          <Grid item xs={2}><Item label="council" icon={<AccountBalanceIcon fontSize='large' />}></Item></Grid>
          <Grid item xs={2}><Item label="general_affair" icon={<ArticleIcon fontSize='large' />}></Item></Grid>
          <Grid item xs={2}><Item label="social_welfare" icon={<TagFacesIcon fontSize='large' />}></Item></Grid>
          <Grid item xs={2}><Item label="public_health_and_sanitation" icon={<LocalHospitalIcon fontSize='large' />}></Item></Grid>
          <Grid item xs={2}><Item label='labor' icon={<EngineeringIcon fontSize='large' />}></Item></Grid>
          <Grid item xs={2}><Item label="agriculture_forestry_and_fisheries" icon={<AgricultureIcon fontSize='large' />}></Item></Grid>
          <Grid item xs={2}><Item label="commerce_and_industry" icon={<CurrencyYenIcon fontSize='large' />}></Item></Grid>
          <Grid item xs={2}><Item label="civil_engineering" icon={<EditRoadIcon fontSize='large'/>}></Item></Grid>
          <Grid item xs={2}><Item label="firefighting" icon={<FireTruckIcon fontSize='large'/>}></Item></Grid>
          <Grid item xs={2}><Item label="education" icon={<SchoolIcon fontSize='large' />}></Item></Grid>
          <Grid item xs={2}><Item label="debt_service" icon={<MoodBadIcon fontSize="large" />}></Item></Grid>
          <Grid item xs={2}><Item label="sundry" icon={<QuestionMarkIcon fontSize='large' />}></Item></Grid>
          <Grid item xs={2}><Item label="reserve" icon={<SavingsIcon fontSize='large'/>}></Item></Grid>
        </Grid>
      )
    else
      return (<div></div>)
  }

  // jsonlabelGrid
  // useEffect(() => {
  //   let yosan_: yosanObject = yosan;
  //   let himokus = [];
  //   Object.keys(yosan_).forEach((key) => {
  //     himokus.push(React.createElement(yosan_[key].label));
  //   });

  // });

  return (
    <div className="App">
      <h1></h1>
      <div>4</div>
      <FormControl >
        <FormLabel id="household"></FormLabel>
        <RadioGroup defaultValue="single" row onChange={handleRadio}>
          <FormControlLabel value="single" control={<Radio />} label="" />
          <FormControlLabel value="family" control={<Radio />} label="" />
        </RadioGroup>
      </FormControl>
      <hr></hr>
      <Box sx={{ width: 300, margin: "auto" }}>
        <>
           {income} 
        </>
        <Slider
          aria-label="Always visible"
          defaultValue={3000000}
          getAriaValueText={valuetext}
          valueLabelDisplay="auto"
          step={1000}
          min={1000000}
          max={20000000}
          onChange={handleChange}
        />
        <div>
           {annual_tax} 
        </div>
      </Box>
      <hr></hr>
      <div></div>
      {makeGrid()}
    </div>
  );
}

export default App;
